.menu_container{
    height: 100%;
    width: 100%;
    background-color: #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    
    
    // D17B88 ,D999B9 B8B3E9 90E0F3 75F4F4 // 414535

}
.menu_item {
    margin: 0.8%;
    font-size: 1.2rem;
    font-family: system-ui;
    font-weight: 600;
    background-color: rgb(75 64 60);
    border-color: transparent;
    border-radius: 12px;
    padding: .4%;
}

.menu_item a{
    color: white;
    text-decoration: none;
    // font-size: 1.5rem;
}

.menu_item :hover{
    // margin: 1%;
